"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Upload, CheckCircle2, XCircle } from "lucide-react";
import InvalidQRUI from "./invalidQRUI";
import { observer } from "mobx-react-lite";
import { useStore } from "@/stores/StoreProvider";
import { toast } from "sonner";

const ClaimPage = ({ shopId }) => {
    const { shopStore } = useStore();

    const [file, setFile] = useState(null);
    const [preview, setPreview] = useState(null);
    const [loading, setLoading] = useState(false);
    const [result, setResult] = useState(null);
    const [isInvalid, setIsInvalid] = useState(false); // <-- main fix

    /** 1ï¸âƒ£ If shopId is missing, immediately show Invalid QR */
    if (!shopId) {
        return <InvalidQRUI />;
    }

    /** 2ï¸âƒ£ Validate shopId from API */
    useEffect(() => {
        const checkShop = async () => {
            try {
                const res = await fetch("/api/public/checkShop", {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify({ shopId }),
                });

                const data = await res.json();

                if (!data.exists) {
                    setIsInvalid(true); // <-- correct way to trigger UI
                }
            } catch (error) {
                console.error(error);
                toast.error(error.message);
            }
        };

        if (shopId) {
            checkShop();
        }
    }, [shopId]);

    /** 3ï¸âƒ£ If shopId is invalid â†’ show the UI */
    if (isInvalid) {
        return <InvalidQRUI />;
    }

    /** 4ï¸âƒ£ File upload handlers */
    const handleFileChange = (e) => {
        const selected = e.target.files?.[0];
        if (!selected) return;

        setFile(selected);
        setPreview(URL.createObjectURL(selected));
    };

    const handleSubmit = async () => {
        if (!file) return alert("Please select a screenshot");

        setLoading(true);
        setResult(null);

        const form = new FormData();
        form.append("file", file);
        form.append("shopId", shopId);

        const res = await fetch("/api/verify", {
            method: "POST",
            body: form,
        });

        const data = await res.json();
        setLoading(false);

        if (data.success) {
            setResult({ success: true });
        } else {
            setResult({ success: false, error: data.error || data.rejectReason });
        }
    };

    return (
        <div className="flex justify-center items-center bg-custom-gradient h-screen">
            <Card className="w-full max-w-md shadow-xl border rounded-2xl">
                <CardHeader className="text-center bg-linear-to-br from-indigo-500 to-purple-600 text-white rounded-t-2xl pb-6">
                    <CardTitle className="text-xl font-semibold">Claim Your Stamp</CardTitle>
                    <p className="text-white/90 text-sm">Upload your UPI payment screenshot</p>
                </CardHeader>

                <CardContent className="flex flex-col gap-6 py-6">
                    {/* UPLOADED IMAGE */}
                    <div className="flex justify-center">
                        {preview ? (
                            <div className="rounded-xl overflow-hidden shadow-lg border">
                                <Image
                                    src={preview}
                                    alt="preview"
                                    width={280}
                                    height={300}
                                    className="object-cover"
                                />
                            </div>
                        ) : (
                            <div className="flex flex-col items-center gap-2 p-6 border-2 border-dashed rounded-xl text-gray-500 w-full text-center">
                                <Upload className="w-8 h-8 opacity-70" />
                                <p className="text-sm">Upload Screenshot</p>
                                <p className="text-xs text-gray-400">PNG / JPG / JPEG</p>
                            </div>
                        )}
                    </div>

                    {/* FILE INPUT */}
                    <label className="flex flex-col gap-2">
                        <p className="text-sm font-medium">Select Screenshot</p>
                        <input
                            type="file"
                            accept="image/*"
                            onChange={handleFileChange}
                            className="border p-2 rounded-md w-full"
                        />
                    </label>

                    {/* SUBMIT BUTTON */}
                    <Button
                        onClick={handleSubmit}
                        disabled={loading || !file}
                        className="w-full py-2 text-md font-medium"
                    >
                        {loading ? "Verifying..." : "Submit Screenshot"}
                    </Button>

                    {/* RESULT */}
                    {result?.success && (
                        <Alert className="bg-green-50 border-green-300">
                            <CheckCircle2 className="h-5 w-5 text-green-700" />
                            <AlertTitle className="text-green-700 font-semibold">Payment Verified!</AlertTitle>
                            <AlertDescription className="text-green-600">
                                Stamp added successfully ðŸŽ‰
                            </AlertDescription>
                        </Alert>
                    )}

                    {result?.error && (
                        <Alert variant="destructive">
                            <XCircle className="h-5 w-5" />
                            <AlertTitle className="font-semibold">Verification Failed</AlertTitle>
                            <AlertDescription>{result.error}</AlertDescription>
                        </Alert>
                    )}
                </CardContent>
            </Card>
        </div>
    );
};

export default observer(ClaimPage);
